.bg-video {
  position: absolute;
  top: 0;
  height: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  opacity: 0.15;
  overflow: hidden;

  &__content {
    height: 100%;
    width: 100%;
    object-fit: cover; // similar to bg size make to coveer it works
    /// with html elements for example videoes
  }
}
